    closed: function () { alert('') }
  },
  created: function () {
    $.ajax({
      ContentType: 'application/json; charset=utf-8',
      dataType: 'json',
      url: '/resource/information/',
      type: 'POST',
      data: {'resource_id': 1},
      success: function (data) {
        alert(data['resource_info']['id'])
      },
      error: function () {
        alert('fail')
      }
    })
  }
}
</script>

<style scpoed>
